// 获取公共域名地址
import serverUrl from '@/common/serverUrl.js';
export default {
	name: "swiper",
	data() {
		return {
			// 图片数组
			swiperImgList: []
		}
	},
	props: {
		swiperImgListPay: {
			type: Array
		}
	},
	created() {

		// 请求轮播图数据
		const qq = serverUrl.qq;
		const url = serverUrl.serverUrl;
		uni.request({
			url: url + '/index/carousel/list?qq=' + qq,
			method: 'POST',
			success: res => {
				console.log(res);
				if (res.data.status == 200) {
					this.swiperImgList = res.data.data;
				}
			},
			fail: () => {
				console.log('获取轮播图数据失败');
			}
		});

	}
}
